<template>
  <tas-base-crud :config="config" hide-create hide-update>
    <template v-slot:list-action>
      <a v-if="$_sys.isAllowed(config.permission.create)" v-b-modal.modal-add-user class="btn btn-primary btn-icon btn-circle ml-3" v-b-tooltip.top="$t('global.add_new')">
        <i class="ri-add-line p-0"></i>
      </a>
      <b-modal id="modal-add-user" hide-footer hide-header size="md">
        <h3 class="card-title mb-0">
          <span class="card-label font-weight-bolder text-dark">Tambah Pengguna</span>
        </h3>
        <hr>
        <form autocomplete="off">
          <s-input v-model="mainForm.sso_user_id" :item="{ label: 'SSO User', type:'lookup-radio', validation:[], placeholder: '', api:'sso/user', param:{ application_code : 'kiw-hrm-v2' }, pointer: { label: 'username', code: 'id', display: ['username', 'fullname', 'nip', 'email'] } }" />
          <s-input v-model="mainForm.role_id" :item="{ label: 'Role Pengguna', type:'lookup-radio', validation:[], placeholder: '', api:'roles', pointer: { label: 'role_name', code: 'id', display: ['role_name', 'role_code'] } }" />
          <s-input v-model="mainForm.employee_id" :item="{ label: 'Karyawan / Tenaga Kerja', type:'lookup-radio', validation:[], placeholder: '', api:'custom/employees-no-user', pointer: { label: 'fullname', code: 'id', display: ['fullname', 'nip', 'email'] } }" />
        </form>
        <hr>
        <div class="text-center">
          <button @click="sumbitUser()" class="btn btn-success">Simpan pengguna</button>
        </div>
      </b-modal>
    </template>
    <!-- <template v-slot:list-table-action="data">
      <a @click="changeUserStatus(data.rowData)" class="btn btn-icon btn-danger btn-sm" v-b-tooltip.viewport="'Klik untuk menonaktifkan pengguna'"> <i class="ri-shut-down-line"></i> </a>
    </template> -->
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-users',
  data () {
    return {
      config: {
        title: 'Daftar Pengguna',
        model_api: 'users',
        getter: 'users',
        setter: 'users',
        pk_field: 'username',
        export: true,
        permission: {
          create: 'create-users',
          read: 'view-users',
          update: 'update-users',
          delete: 'delete-users'
        },
        fields: [
          { id: 'fullname', label: 'Karyawan / Tenaga Kerja' },
          { id: 'username', label: 'SSO Username' },
          { id: 'telephone', label: 'No. Telp' },
          { id: 'nip', label: 'NIP (Nomor Induk Pegawai)' },
          { id: 'last_login_at', label: 'Login Terakhir', methods: { list: { transform: 'longDateTime' } } },
          {
            id: 'email_verified_at',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          {
            id: 'approval_at',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          {
            id: 'approval_by',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          {
            id: 'approval_description',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          {
            id: 'password',
            methods: { list: false, detail: false, create: true, update: false, filter: false }
          },
          {
            id: 'id',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          // updated_by
          {
            id: 'updated_by',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          // created_by
          {
            id: 'created_by',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          // created_at
          {
            id: 'created_at',
            label: 'Dibuat',
            methods: { create: false, update: false, filter: false }
          },
          // updated_at
          {
            id: 'updated_at',
            label: 'Diperbarui',
            methods: { list: false, detail: false, create: false, update: false, filter: false }
          },
          {
            id: 'img_photo_user',
            label: 'Foto Pengguna',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_photo_user.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }],
                view_data: 'img_photo_user'
              }
            }
          },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status Verifikasi',
            placeholder: null,
            methods: {
              list: false,
              detail: {
                order: true,
                class: { email_unverified: 'badge badge-danger', email_verified: 'badge badge-primary', user_active: 'badge badge-success' },
                transform: 'status_code'
              },
              create: false,
              update: false,
              filter: false
            }
          },
          {
            id: 'role_id',
            label: 'Role',
            methods: {
              create: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              },
              update: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              },
              filter: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              }
            }
          }
        ]
      },
      mainForm: {
        sso_user_id: null,
        role_id: null,
        employee_id: null
      }
    }
  },
  methods: {
    sumbitUser () {
      this.$_api.post('sso/user/create', this.mainForm).then(res => {
        this.$_alert.success(null, res.message)
        this.$children[0].$children[1].getData(null)
        this.successAction(true)
      }, err => {
        this.$_alert.error(err)
      })
    },
    successAction (hideModal = false) {
      this.mainForm = Object.assign({}, {
        sso_user_id: null,
        role_id: null,
        employee_id: null
      })
      if (hideModal) this.$bvModal.hide('modal-add-user')
    },
    changeUserStatus (data) {
      data.status_code = 'user_nonactive'
      this.$_api.post('users/active-deactive', data).then(res => {
        this.$_alert.success(null, res.message)
        this.$children[0].$children[1].getData(null)
      }, err => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
</style>



